import React from 'react';
import { Link } from 'react-router-dom';

const DashboardActions = () => {
  return (
    <div class='dash-buttons'>
      <Link to='/edit-profile' class='btn btn-light'>
        <i class='fas fa-user-circle text-primary' /> Edit Profile
      </Link>
      <Link to='/add-Interests' class='btn btn-light'>
        <i class='fab fa-black-tie text-primary' /> Add Interests
      </Link>
      <Link to='/add-Major' class='btn btn-light'>
        <i class='fas fa-graduation-cap text-primary' /> Add Major
      </Link>
    </div>
  );
};

export default DashboardActions;
